<svelte:options customElement="fds-assets"></svelte:options>

<script>
    import Sortable from 'sortablejs'

    export let assets = []
    let dragStatus = 'none'
    let open = false
    let repeaterElement;
    let currentPath = [] // Array of folder IDs representing current location
    let sortableInstance = null
    // Get current folder based on currentPath
    let currentFolder = { children: assets }

    let showPropertiesDialog = false;
    let selectedAsset = null;
    let editName = '';
    let editDescription = '';
    // Reagiert auf Änderungen von currentPath UND assets
    $: updateCurrentFolder(currentPath, assets)
    
    function updateCurrentFolder(path, assets) {
        let folder = { children: assets }
        try {
            for (const fid of path) {
                folder = folder.children.find(c => c.id === fid)
                if (!folder) throw new Error('Invalid path')
            }
        } catch {
            currentPath = []
            folder = { children: assets }
        }
        currentFolder = folder
    }


    // Navigation functions
    function navigateToFolder(folderId) {
        currentPath = [...currentPath, folderId]
        currentFolder=currentFolder        

    }


    // Helper function für Bildstile
    function getImageStyle(meta) {
        if (meta.style) return meta.style;
        if (!meta || !meta.width) {
            return "width: auto; height: 112px;"
        }
        if (meta.width === meta.height) {
            return "height: 112px; width: 112px;"
        }
        if (meta.width > meta.height) {
            return "width: 200px; height: auto;"
        }
        return "width: auto; height: 112px;"
    }

    // File Upload Handlers
    function handleFileDragOver(event) {
        if (!event.dataTransfer?.items?.length) return;
        
        const hasFiles = Array.from(event.dataTransfer.items).some(item => item.kind === 'file');
        if (!hasFiles) return;

        event.preventDefault();
        const hasCompatibleFiles = Array.from(event.dataTransfer.items).some(item => {
            return checkAgainstPlugins(item)
        });
        dragStatus = hasCompatibleFiles ? 'valid' : 'invalid';
    }

    function handleFileDragLeave(event) {
        event.preventDefault();
        dragStatus = 'none';
    }

    async function handleFilesDrop(event) {
        event.preventDefault();
        event.stopPropagation();
        dragStatus = 'none';

        const files = Array.from(event.dataTransfer.files);        
        for (const file of files) {
            const matchingPlugins = [];
            for (let tag in globalThis.gyre.plugins) {
                const pluginInfo = globalThis.gyre.plugins[tag].options;
                if (pluginInfo.type === "asset" && checkFileType(file, pluginInfo.allowedTypes)) {
                    matchingPlugins.push({
                        tag: tag,
                        name: pluginInfo.name || tag,
                        description: pluginInfo.description || ""
                    });
                }
            }
    
            if (matchingPlugins.length === 0) continue;
    
            if (matchingPlugins.length === 1) {
                await processWithPlugin(matchingPlugins[0].tag, 'importFileAsAsset', file,currentFolder)
                currentFolder=currentFolder

                continue;
            }


            let selectedTag=await openMenu(event, matchingPlugins.map(p => ({
                id: p.tag,
                name: p.description ? `${p.name} (${p.description})` : p.name,
                key: p.tag
            })))

            await processWithPlugin(selectedTag,'importFileAsAsset', file,currentFolder)
            currentFolder=currentFolder
        }
    }
    let selectedMenuItem={}
    async function openMenu(event,items,element=null) {
        let doc=window.document
        const contextMenu = doc.createElement("fds-image-editor-menu");
            contextMenu.menu = items
            //contextMenu.showsubmenudot = true;
            if (element) contextMenu.element=element
            else {
                contextMenu.x = event.clientX
                contextMenu.y = event.clientY                     
            }
            doc.body.appendChild(contextMenu)
            const selection = await new Promise(resolve => {
                contextMenu.callback = (item) => {
                    doc.body.removeChild(contextMenu)
                    selectedMenuItem=item
                    resolve(item.key);
                }
            })

            return selection
    }
    /**
     * add new asset:
     * - empty folder
     * - empty asset if createEmpty:true is set
     * - upload (same as drop) - not supported yet
     */
    async function add(event) {
        let items=[{id:1,name:"Upload Media...",key:"upload"},{id:2,name:"Add Folder",key:"folder"}]
        let id=3
        for (let tag in globalThis.gyre.plugins) {
            const pluginInfo = globalThis.gyre.plugins[tag].options;
            if (pluginInfo.type === "asset" && pluginInfo.createEmpty) {
                items.push({name:"New "+pluginInfo.name,id:id++,key:"new_"+tag,tag:tag})
            }
        }
        let selection=await openMenu(event,items)
        if (selection==="folder") {
            let folder={
            name: "New Folder",
            id: crypto.randomUUID(),
            type: "folder",   
            meta: {
                style:  "width: 50px; height: 50px;"
            },
            children:[],
            thumbnail:  'data:image/svg+xml;base64,'
            }
            currentFolder.children.push(folder)
            currentFolder=currentFolder

        }         
        if (selection.startsWith("new_")) {
            await processWithPlugin(selectedMenuItem.tag, 'createEmptyAsset',currentFolder)
            currentFolder=currentFolder

        }     
    }
    /**
     * context menu of one asset
     * @param event
     * @param asset
     * - delete asset
     * - move to other folder
     * - edit meta data
     * - optional: clone asset if hasClone is true in plugin info
     * - optional: contextMenu entries (contextMenu array)
     */
    async function assetSettingsMenu(event,asset) {
        let pluginInfo={}
        if (asset.type!=="folder") pluginInfo = globalThis.gyre.plugins[asset.type].options
        let items=[{id:1,name:"Delete",key:"delete"},{id:2,name:"Move...",key:"move"},{id:2,name:"Properties",key:"meta"}]
        if (pluginInfo.hasClone) {
            items.push({id:1,name:"Clone",key:"clone"})
        } 
        if (pluginInfo.contextMenu && pluginInfo.contextMenu.length) {
            for(let i=0;i<pluginInfo.contextMenu.length;i++) {
                let entry=pluginInfo.contextMenu[i]
                items.push({id:1,name:entry.name,key:entry.key,method:entry.method})
            }

        }       
        let key=await openMenu(event,items)
        if (key==="delete") return await deleteAsset(asset)
        if (key==="move") return await moveAsset(event,asset)
        if (key === "meta") {
            selectedAsset = asset
            editName = asset.name
            editDescription = asset.description || ''
            showPropertiesDialog = true
            return
        }
        if (key==="clone") {
            await processWithPlugin(asset.type, 'cloneAsset',asset,currentFolder)
            currentFolder=currentFolder
            return
        }
        let entry=selectedMenuItem
        await processWithPlugin(asset.type, entry.method,asset,currentFolder)

        
    }
    function saveProperties() {
        if (selectedAsset) {
            selectedAsset.name = editName;
            selectedAsset.description = editDescription;
            // Force update
            currentFolder.children = currentFolder.children;
        }
        showPropertiesDialog = false;
    }    
    function checkAgainstPlugins(file) {
        for(let tag in globalThis.gyre?.plugins) {
            let pluginInfo = globalThis.gyre.plugins[tag].options;
            if (pluginInfo.type === "asset") {
                if (checkFileType(file, pluginInfo.allowedTypes)) {
                    return true;
                }
            }
        }
        return false;
    }

    function checkFileType(file, allowedTypes) {
        return allowedTypes.some(type => {
            if (type.startsWith('.') && file.name?.toLowerCase().endsWith(type)) {
                return true;
            }
            return file.type === type;
        });
    }
    // execute method of a plugin
    async function processWithPlugin(tag, methodName, ...methodParams) {
        const doc = window.document;
        let pluginComponent = doc.body.querySelector(`:scope > ${tag}`);
        let isNewComponent = false;

        if (!pluginComponent) {
            pluginComponent = doc.createElement(tag);
            doc.body.appendChild(pluginComponent);
            isNewComponent = true;
        }

        await pluginComponent.ready;
        let res=await pluginComponent[methodName](...methodParams);

        if (isNewComponent) {
            doc.body.removeChild(pluginComponent);
        }
        return res
    }
    // open asset for editing
   async  function openAsset(asset) {
        if (asset.type==="folder") {
            navigateToFolder(asset.id)
            return
        }
        let pluginInfo = globalThis.gyre.plugins[asset.type].options
        if (pluginInfo.openAsset==="off" || pluginInfo.openAsset==="none") return 
        if (pluginInfo.openAsset==="default") {
            window.open('./asset.html?assetId='+asset.id+"&docId="+globalThis.gyre.docId, '_blank')
            return
        }
        await processWithPlugin(asset.type, 'openAsset', asset)
    }
    function initializeSortable() {
        if (sortableInstance) sortableInstance.destroy()
        sortableInstance = Sortable.create(repeaterElement, {
                animation: 200,
                easing: "cubic-bezier(1, 0, 0, 1)",
                ghostClass: 'sortable-ghost',
                dragClass: 'sortable-drag',
                chosenClass: 'sortable-chosen',
                filter: '.no-drag',  // Elemente mit dieser Klasse werden vom Sorting ausgeschlossen
                forceFallback: false,
                fallbackClass: 'sortable-fallback',
                onMove: function(evt) {
                    // Verhindere das Ziehen VOR oder AUF das Menu-Item
                    return !evt.related.classList.contains('menu-item');
                },
                onEnd: (evt) => {
    // Menu-Item ist bei DOM-Index 0 -> verschiebe um 1
    const oldIndex = evt.oldIndex - 1;
    const newIndex = evt.newIndex - 1;

    // Wenn -1 dann oberhalb des Plus-Elements -> Abbruch oder wie auch immer du das handeln willst:
    if (oldIndex < 0 || newIndex < 0) {
        return; 
    }

    const newChildren = [...currentFolder.children];
    const [removed] = newChildren.splice(oldIndex, 1);
    newChildren.splice(newIndex, 0, removed);
    currentFolder.children = newChildren;
    assets = assets; // trigger Svelte update
}

            });
        }

        $: if (repeaterElement) initializeSortable()
   
        function getFolderName(id) {
    let folder = { children: assets };
    for (const fid of currentPath) {
        folder = folder.children.find(c => c.id === fid);
        if (!folder) return '?';
    }
    return folder?.name || 'Unbekannt';
}
    async function deleteAsset(asset) {
        if (!window.confirm(`Do you really want to delete "${asset.name}"?`)) {
            return;
        }        
        if (asset.type === "folder") {
            if (asset.children && asset.children.length > 0) {
                window.alert("The folder is not empty.");
                return;
            }
            currentFolder.children = currentFolder.children.filter(a => a.id !== asset.id);
            currentFolder = currentFolder; 
            return
        }
        await processWithPlugin(asset.type, 'deleteAsset', asset)

        currentFolder.children = currentFolder.children.filter(a => a.id !== asset.id)
        currentFolder = currentFolder; 
    }


    async function moveAsset(event,asset) {
        let items=buildFolderMenu({ id: 'root', children: assets }, asset)
        let menuItems=[{name:"Top",key:"_top_",items:items}]
        let selectedFolderId=await openMenu(event,menuItems)
        if (!selectedFolderId) return;
        // Im Datamodel den Zielordner finden
        const targetFolder = findFolderById(assets, selectedFolderId);
        if (!targetFolder) return;
        // Nur verschieben, wenn es auch ein Ordner ist
        if (targetFolder.type !== 'folder') return;

        // Asset aus aktuellem Folder entfernen
        const index = currentFolder.children.findIndex(a => a.id === asset.id);
        // If found, remove it using splice (mutates the original array)
        if (index !== -1) {
            currentFolder.children.splice(index, 1);
        }
        // Asset in den Zielordner einfügen
        targetFolder.children = targetFolder.children || [];
        targetFolder.children.push(asset);
        // Svelte anstoßen
        currentFolder = currentFolder;
    }

    // helper function
    function buildFolderMenu(folder, assetToMove,i=1) {
    let items = {}
    if (!folder.children) return items;

    const isDescendant = (parent, childId) => {
        if (parent.id === childId) {
            return true;
        }
        if (parent.children) {
            for (const c of parent.children) {
                if (isDescendant(c, childId)) {
                    return true;
                }
            }
        }
        return false;
    };

    for (let child of folder.children) {
        if (child.type === 'folder') {
            // Überspringen, wenn es der assetToMove selbst oder ein Unterordner davon ist
            if (child.id === assetToMove.id || isDescendant(assetToMove, child.id)) {
                continue;
            }
            let entry={
                id: i++,
                key: child.id,
                name: child.name,
             //   items: 
            }
            let subItems=buildFolderMenu(child, assetToMove,i)
            if (Object.keys(subItems).length) entry.items=subItems
            items[entry.key]=entry
        }
    }

    return items;
}

    // helper function
    function findFolderById(list, folderId) {
        if (folderId==="_top_") return {name:"root",children:list,type:"folder"}
        for (let entry of list) {
            if (entry.id === folderId && entry.type === 'folder') {
                return entry;
            }
            if (entry.children && entry.children.length) {
                const result = findFolderById(entry.children, folderId);
                if (result) return result;
            }
        }
        return null;
    }
    let settingsButtonDIV
</script>

<style>
