    .breadcrumb span {
        opacity: 0.6;
        user-select: none;
    }
    
    .breadcrumb button {
        padding: 4px 8px;
        background: none;
        border: none;
        cursor: pointer;
    }
    
    .breadcrumb button:hover {
        background: rgba(0,0,0,0.05);
    }

