    }

    .drag-over-invalid {
        background-color: rgba(244, 67, 54, 0.1);
        outline: 2px dashed #F44336;
    }
    .menu-plus {
        width: 50px;
    stroke: black;
    position: absolute;
    left: calc(50% - 25px);
    top: calc(50% - 25px);
    }
    .no-drag:hover {
        background-color: #ddb74f;
        transition: 0.6s;
        cursor:pointer;
    }
    .breadcrumb {
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 8px 16px;
        background: #f5f5f5;
        border-bottom: 1px solid #ddd;
    }    
    .formInput {
        width: 100%;
        outline: 0;
        border: 0;
        border-bottom: 2px solid rgba(255, 255, 255, 0.2);
        margin-bottom: 10px;
        padding: 5px;
        font-size: 14px;
        font-weight: normal;
        border-radius: 3px;
        color: rgba(255, 255, 255, 0.9);
        display: block;
        background: none;
        font-family: system-ui, 'Segoe UI', Roboto;
    }

    .formInput {
        border-bottom: 2px solid rgba(255, 255, 255, 0.2);

    }   
    label, p {
        color: rgb(193, 193, 193);
    } 
</style>

<!-- svelte-ignore a11y-click-events-have-key-events -->
<!-- svelte-ignore a11y-no-static-element-interactions -->
<fds-image-editor-button type="button" on:click={()=>{open=true}} state="active">Assets</fds-image-editor-button>

{#if open}
    <!-- svelte-ignore a11y-no-static-element-interactions -->
    <fds-image-editor-dialog 
        {open} 
        fullscreenmode={true} 
        usepopover={true} 
        onclosed={()=>{open=false;}}
        on:dragover|preventDefault|stopPropagation={handleFileDragOver}
        on:dragleave|preventDefault|stopPropagation={handleFileDragLeave}
        on:drop|preventDefault|stopPropagation={handleFilesDrop}>
        <span slot="modalcontent">
            <div class="fullScreen" class:drag-over-valid={dragStatus === 'valid'} class:drag-over-invalid={dragStatus === 'invalid'}>
<!-- Im Template-Teil -->
<div class="breadcrumb">
    {#if currentPath.length > 0}
        <!-- svelte-ignore a11y-click-events-have-key-events -->
        <fds-image-editor-button 
            icon="chevronUp" 
            on:click={() => currentPath = currentPath.slice(0, -1)}
        ></fds-image-editor-button>
    {/if}

    {#each currentPath as _, index}
        {#if index === 0}
            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button type="button"
                on:click={() => currentPath = currentPath.slice(0, index + 1)}>
                {getFolderName(currentPath[index])}
            </fds-image-editor-button>
        {:else}
            <span>/</span>
            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button  type="button"
                on:click={() => currentPath = currentPath.slice(0, index + 1)}>
                {getFolderName(currentPath[index])}
            </fds-image-editor-button>
        {/if}
    {/each}
</div>
                <div class="repeater" bind:this={repeaterElement}>
                    <!-- Menu Item -->
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                    <div class="repeater-item menu-item no-drag"  on:click|stopPropagation={(event) => { add(event) }} 
                        >
                        <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" class="menu-plus" viewBox="0 0 16 16">
                            <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
                        </svg>
                    </div>
                    
                    <!-- Regular Items -->
                    {#each currentFolder.children as asset (asset.id)}
                        <div class="repeater-item">
                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <fds-image-editor-button 
                                icon="moreSettings" 
                                style="--theme-btn-text-color:red" 
                                bind:this={settingsButtonDIV}
                                on:click|stopPropagation={(event) => assetSettingsMenu(event,asset)} 
                                class="settings"
                            />
                  
                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <div class="title" alt="{asset.description}" on:click|stopPropagation={(event) => { openAsset(asset) }}>{asset.name}</div>
                            {#if asset.thumbnail}
                                <div class="image-container">
                                    <img 
                                        class="image"
                                        src={asset.thumbnail}
                                        alt={asset.name}
                                        style={getImageStyle(asset.meta)}
                                        draggable="false"
                                    >
                                </div>
                            {:else}
                                <div>No Thumbnail</div>
                            {/if}
                        </div>
                    {/each}
                </div>
            </div>
                    {#if showPropertiesDialog}
                        <fds-image-editor-dialog
                            open={showPropertiesDialog}
                            onclosed={() => showPropertiesDialog = false}
                            nobackdrop={true}
                            style="--dialog-bgcolor: black;"
                        >
                            <div slot="modalcontent" class="properties-dialog">
                                <p>Edit Properties</p>
                                <div class="form-group">
                                    <!-- svelte-ignore a11y-label-has-associated-control -->
                                    <label>Name:</label>
                                    <input type="text" bind:value={editName} class="formInput">
                                </div>
                                <div class="form-group">
                                    <!-- svelte-ignore a11y-label-has-associated-control -->
                                    <label>Description:</label>
                                    <textarea bind:value={editDescription}  class="formInput"/>
                                </div>
                                <div class="dialog-actions">
                                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                                    <fds-image-editor-button type="button"  on:click={() => showPropertiesDialog = false}>
                                        Cancel
                                    </fds-image-editor-button>
                                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                                    <fds-image-editor-button type="button"  on:click={saveProperties} state="active">
                                        Save
                                    </fds-image-editor-button>
                                </div>
                            </div>
                        </fds-image-editor-dialog>
                {/if}
    </span>
    </fds-image-editor-dialog>
{/if}
